<template>
  <div id="app">
    <h1>TodoList记事本</h1>
    <TopSearch :addItem='addItem'/>
    <ListItem :todoList='todoList' :deleteItem='deleteItem' :myDone="myDone"/>
    <TheFooter :todoList='todoList' :deleteTodo="deleteTodo"/>
  </div>
</template>

<script>
import ListItem from './components/ListItem.vue'
import TopSearch from './components/TopSearch.vue'
import TheFooter from './components/TheFooter.vue'
import {nanoid} from 'nanoid'
export default {
  name:'App',
  data(){
    return {
      todoList:JSON.parse(localStorage.getItem('todoList')) || []
    }
  },
  watch:{
    todoList:{
      deep:true,
      handler(value){
        localStorage.setItem('todoList',JSON.stringify(value))
      }
    }
  },
  methods:{
    addItem(x){
      if(x === '') return 
      const arr = {
        id:nanoid(),
        title:x,
        isDone:false
      }
      this.todoList.unshift(arr)

    },
    deleteItem(x){
      if(confirm('确认删除吗？')){
        for(let i = 0;i<this.todoList.length;i++){
          if(this.todoList[i].id == x){
            this.todoList.splice(i,1)
          }
        }
      }
      
    },
    myDone(x){
      // console.log('我要修改了')
      for(let i = 0;i<this.todoList.length;i++){
        if(this.todoList[i].id === x){
          this.todoList[i].isDone === true ? this.todoList[i].isDone = false : this.todoList[i].isDone = true
        }
      }
    },
    deleteTodo(){
      
      let n = this.todoList.length
      for(let i = 0;i<n;i++){
        if(this.todoList[i].isDone){
          this.todoList.splice(i,1)
          n--
          i--
        }
      }

    },
    handlerEdit(todoId){
      for(let i =0;i<this.todoList.length;i++){
        if(this.todoList[i].id === todoId){
          if(this.todoList[i].hasOwnProperty('isEdit')){
            this.todoList[i].isEdit = true
          }
          else{
            this.$set(this.todoList[i],'isEdit',true)
          }
        }
      }
      
    },
    changeEdit(todoId,newTitle){

      for(let i =0;i<this.todoList.length;i++){
        if(this.todoList[i].id === todoId){
          if(!newTitle) {
            this.todoList[i].isEdit = false
            return alert('输入不能为空！！！')
          }   
          this.todoList[i].title = newTitle
          this.todoList[i].isEdit = false
        }
      }
    },
    checkAll(){
      this.todoList.forEach(element => {
        element.isDone = true
      });
    },
    unCheckAll(){
       this.todoList.forEach(element => {
        element.isDone = false
      });
    }
  },
  components:{TopSearch,ListItem,TheFooter},
  mounted(){
   this.$bus.$on('handlerEdit',this.handlerEdit)
   this.$bus.$on('changeEdit',this.changeEdit)
   this.$bus.$on('checkAll',this.checkAll)
   this.$bus.$on('unCheckAll',this.unCheckAll)
  }
}
</script>

<style>
#app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 800px;
  margin: 0 auto;
}
#app div{
  margin-top: 10px;
}
@media(max-width:770px){
   #app{
    width: 80%;
   }
}
</style>
