<template>
  <div class="item">
    <label>
        <input type="checkbox" @click.stop="myDone(todo.id)" :checked='todo.isDone'>
        <span>
            <template v-if="!todo.isEdit">
                {{todo.title}}
            </template>
            <input 
            type="text" 
            :value="todo.title" 
            v-if="todo.isEdit" 
            @blur="handlerBlur" 
            @click.prevent=""
            ref="Edit"
            >
        </span>
    </label>
    <div class="btns">
        <button class="btn" @click="handlerEdit">编辑</button>
        <button class="btn" @click="deleteItem(todo.id)" :deleteItem='deleteItem'>删除</button>
    </div>
    
  </div>
</template>

<script>
export default {
    props:['todo',"deleteItem",'myDone'],
    methods:{
        handlerEdit(){
            this.$bus.$emit('handlerEdit',this.todo.id)
            this.$nextTick(function(){
                this.$refs.Edit.focus()
            }) //下一轮渲染
        },
        handlerBlur(e){ 
            this.$bus.$emit('changeEdit',this.todo.id,e.target.value)
        }
    }
}
</script>

<style scoped>
.item{
    display: flex;
    align-items: center;
    width: 700px;
    height:50px;
    color: brown;
    border-radius: 3px;
    font-size: 18px;
    background-color: dodgerblue;
    margin-top: 5px;
    justify-content: space-between;
}
.item label{
    cursor: pointer;
}
.item span{
    margin-left:5px
}
.item span input{
    width: 250px;
    height:25px;
    background-color: rgba(255, 255, 255, 0.4);
    border:1px solid white;
    font-size:18px;
    color:black;
    border-radius:3%
}
.btns{
    display: none;
    margin-bottom: 5px;
}
.btn{
    background-color: orange;
    width:50px;
    height:30px;
    border:0px solid white;
    color:white;
    border-radius:5px;
    margin-right:10px;
    
    cursor: pointer;
}
.item:hover{
    background-color: skyblue;
}
.item:hover .btns{
    
    display: block;
}
@media(max-width:770px){
    .item{
        width: 100%;
        height: 35px;
        font-size: 15px;
        margin-left: 5px;
    }
    .btn{
        height: 20px;
        margin-bottom: 5px;
    }
}
</style>