<template>
  <div class="theFooter">
    <span>
        <label @click="changeCheck">
          <input type="checkbox" v-model="isCheck">全选
        </label>
        <span>今天共完成{{done}}/{{todoList.length}}件事</span>
    </span>
    <button @click="deleteTodo">清除所有已完成事项</button>
  </div>
</template>

<script>
export default {
  data(){
    return {
      isCheck:false
    }
  },
  props:['todoList','deleteTodo'],
  computed:{
    done(){
      var n = 0
      for(let i = 0;i<this.todoList.length;i++){
        if(this.todoList[i].isDone) 
          n++
      }
      return n
    }
  },
  methods:{
    changeCheck(){
      if(this.isCheck){
        this.$bus.$emit('unCheckAll')
        
      }
      else{
        this.$bus.$emit('checkAll')
      }
    }
  }
}
</script>

<style scoped>
.theFooter{
    width:90%;
    display: flex;
    justify-content: space-between;

}
.theFooter>span{
  margin-left: 5px;
}
.theFooter>span>span{
  margin-left: 18px;
}
.theFooter button{
    background-color: darkorange;
    color: aliceblue;
    border: 0px;
    border-radius: 4px;
    padding: 6px;
}
.theFooter button:hover{
  cursor: pointer;
}
@media(max-width:770px){
  .theFooter{
    width: 100%;
  }
  .theFooter>span{
    display: flex;
    flex-direction: column;
    font-size: 14px;
  }
  .theFooter>span>span{
    margin: 0;
  }
  .theFooter button{
    font-size: 12px;
    padding-top: 3px;
    padding-bottom: 3px;
  }
}
</style>