<template>
  <div class="container">
    <input type="text" class="searchBox" @keydown.enter="search" v-model="result" placeholder="请输入要添加的内容，按回车键以确认">
  </div>
</template>

<script>
export default {
    name:"TopSearch",
    data(){
        return {
            result:'',
        }
    },
    methods:{
        search(){
            this.addItem(this.result)
            this.result = ''
        }
    },
    props:['addItem']
}
</script>

<style scoped>
.searchBox{
    border-radius: 8px;
    color: skyblue;
    font-size: 24px;
    background-color: rgba(255, 255, 255, 0);
    width: 700px;
    height:50px;
    padding-left: 15px;
    border:2px solid aqua
}
@media(max-width:770px){
  .container{
    width: 100%;
  }
  .searchBox{
    width: 97%;
    font-size: 12px;
    height: 30px;
  }
}
.searchBox::selection {
  border-color: orange; /* 修改选中文本框的边框颜色 */
  border-style: dashed; /* 修改选中文本框的边框样式 */
  border-width: 2px; /* 修改选中文本框的边框宽度 */
}
</style>